import React from 'react';
import {Component} from 'react';
import { Link } from 'gatsby';
import '../components/header';
import '../style/blog.scss';
import Header from '../components/header';
import Footer from '../components/footer';
import background from '../images/showcase.jpg'
import stairs from '../images/stairs.jpg'
import Showcase from '../components/showcase';
import BlogCard from '../components/BlogCard';
import { Pagination, PaginationItem, PaginationLink } from 'reactstrap';

class Blog extends Component {

  constructor(props) {
    super(props);
    this.state = {
        postPerPage: 6,
        pages: 0
    };
  } 

  componentWillMount(){
    const pages = Math.ceil(this.props.data.allContentfulBlog.edges.length / this.state.postPerPage);
    this.setState({pages: pages})
  }
  

  render() {
    const low = this.props.pageContext.index*6;
    const high = low + 6;
    const post = this.props.data.allContentfulBlog.edges.slice(low, high);
    const pages = Math.ceil(this.props.data.allContentfulBlog.edges.length/6);
    return(
  <>
    <Header color="black"/>
    <Showcase background={background} name='BE RELENTLESS'/>
    <section id="blog-grid" className="mx-5 py-5 my-5">
      {post.map((edge) => {
        return <BlogCard photo={edge.node.photo.file.url} url={edge.node.slug} category={edge.node.category} excerpt={edge.node.excerpt} title={edge.node.title} subTitle={edge.node.subTitle} />
      })}
    </section>
    <div className="container mx-auto">
      <Pagination aria-label="Page navigation example" className="mx-auto">
        <PaginationItem>
          <PaginationLink previous href="#" />
        </PaginationItem>
        {Array.from('x'.repeat(pages)).map((x,i) => {
          return(
          <PaginationItem>
            <PaginationLink href={'/blog/page' + (i + 1)}>
              {i+1}
            </PaginationLink>
          </PaginationItem>
          )
        })}
        <PaginationItem>
          <PaginationLink next href="#" />
        </PaginationItem>
      </Pagination>
    </div>
    
    <Footer />
  </>
);
}
}

export default Blog;

export const postQuery = graphql`
  query postQuery{
   allContentfulBlog {
		edges {
		  node {
		    id
        contentful_id
        slug
        category
        title
        subTitle
        excerpt
        photo{
          file{
            url
          }
        }
		  }
		}
  }
}
`